<template>
    <div class="uploadVideo">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle"></span>
            <span class="back"></span>
            <span class="upload" @click="release">发布</span>
        </div>
        <div class="content">
            <form class="videoForm">
                <div class="details">
                    <label class="detail">标题</label>
                    <input class="videoTitle" type="text" placeholder="为您的视频添加一个醒目的标题吧" v-model="videoTitle">
                </div>
                <div class="details">
                    <label class="detail">内容</label>
                    <textarea class="videoContent" placeholder="添加视频内容，以便大家深入了解" v-model="videoContent"></textarea>
                </div>
                <div class="details">
                    <label class="detail">视频</label>
                    <span class="videoImg" v-show="hasCover">
                        <img :src="videoCover">
                    </span>
                    <label class="getVideo" for="file" @click="selectVideo" v-show="!hasCover"></label>
                    <input type="file" name="file" accept="video/mp4" ref="myVideo" style="display: none;" @change="getCover">
                </div>
                <div class="details">
                    <label class="detail">付费</label>
                    <span class="paid">
                        <span>
                            <input type="radio" class="paidRadio" name="paidRadio" value="1" v-model="paid">
                            <label>是</label>
                        </span>
                        <span>
                            <input type="radio" class="paidRadio" name="paidRadio" value="0" v-model="paid">
                            <label>否</label>
                        </span>
                    </span>
                </div>
                <div class="details" v-if="paid === '1'">
                    <input class="henghazhi" type="number" v-model="videoPrice" step="0.01" min="0.01">
                    <label class="hengha">元</label>
                    <span class="notice">请填写收费金额</span>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { getCover, uploadVideo } from '../../api/getdata'
import { Toast, Indicator } from 'mint-ui'
export default {
  name: 'uploadVideo',
  data () {
    return {
      hasCover: false,
      videoCover: '',
      videoTitle: '',
      videoContent: '',
      paid: '0',
      videoPrice: ''
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getCover () {
      if (this.$refs.myVideo.files.length !== 0) {
        var video = new FormData()
        video.append('file', this.$refs.myVideo.files[0])
        video.append('scene', 'releaseVideoCoach')
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        Indicator.open({
          text: '视频添加中',
          spinnerType: 'snake'
        })
        let data = getCover(video, config)
        data.then(res => {
          console.log(res.data.fullFile)
          this.videoCover = res.data.fullFile
          localStorage.setItem('videoCover', res.data.relaFile)
          this.hasCover = true
          Indicator.close()
          Toast({
            message: '添加成功',
            duration: 1500
          })
        })
      }
    },
    release () {
      if (this.$refs.myVideo !== 0) {
        var reg = /^[0-9]+([.]{1}[0-9]{1,2})?$/
        if (reg.test(this.videoPrice) || this.videoPrice === '') {
          var formdata = new FormData()
          Indicator.open({
            text: '视频上传中',
            spinnerType: 'snake'
          })
          formdata.append('relaFile', localStorage.getItem('videoCover'))
          // console.log(getCookie('videoCover'))
          formdata.append('videoName', this.videoTitle)
          formdata.append('videoNote', this.videoContent)
          formdata.append('open', this.paid)
          formdata.append('videoPrice', this.videoPrice)
          let config = {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
          let data = uploadVideo(formdata, config)
          data.then(res => {
            console.log(res)
            if (res.status === '0') {
              Indicator.close()
              Toast({
                message: '视频上传成功',
                duration: 1500
              })
              this.$router.go(-1)
            } else {
              Toast({
                message: '视频上传失败',
                duration: 1500
              })
            }
          })
        } else {
          Toast({
            message: '视频金额格式不正确',
            duration: 1500
          })
        }
      } else {
        Toast({
          message: '请先添加视频',
          duration: 1500
        })
      }
    },
    selectVideo () {
      this.$refs.myVideo.click()
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    color: #333333;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 99;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .upload {
    position: absolute;
    right: 0;
    font-size: 0.42667rem;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .details {
    display: flex;
    margin: 0.5rem 0;
  }

  .detail {
    font-size: 0.42667rem;
    color: #333333;
  }

  .videoTitle,
  .videoContent {
    margin-left: 0.37333rem;
    border: none;
    outline: none;
    flex-grow: 1;
  }

  .videoTitle {
    border-bottom: 1px solid #c0c0c0;
  }

  .videoContent {
    background: #f5f5f5;
    padding: 0.29333rem 0.42667rem;
    height: 4.74667rem;
    resize: none;
    border-radius: 0.2rem;
  }

  .videoTitle::placeholder,
  .videoContent::placeholder {
    font-size: 0.4rem;
  }

  .getVideo {
    display: block;
    width: 4.26667rem;
    height: 4.26667rem;
    margin-left: 0.37333rem;
    background: #f5f5f5;
    border: none;
    outline: none;
    border-radius: 0.2rem;
    background-image: url('../../assets/img/addUpload.png');
    background-position: center;
    background-size: 1.2rem 1.2rem;
    background-repeat: no-repeat;
  }

  .paidRadio {
    width: 0.21333rem;
    height: 0.21333rem;
    border-radius: 50%;
    outline: none;
    background: transparent;
    z-index: 99;
    position: relative;
  }

  .paidRadio:last-child {
    margin-left: 2rem;
  }
  .paidRadio::after {
    content: '';
    position: absolute;
    display: block;
    width: 0.34667rem;
    height: 0.34667rem;
    border-radius: 50%;
    border: 2px solid #f5f5f5;
    top: -0.10667rem;
    left: -0.09333rem;
  }

  .paidRadio:checked {
    background: #ffd200;
  }

  .paid {
    margin-left: 0.6rem;
    display: flex;
    align-items: center;
    width: 4rem;
    justify-content: space-between;
  }

  .paid label {
    margin-left: 0.21333rem;
    font-size: 0.42667rem;
    color: #333333;
  }

  .paid span {
    display: flex;
    align-items: center;
  }

  .henghazhi {
    border: 1px solid #ffd200;
    border-radius: 0.2rem;
    outline: none;
    width: 2.98667rem;
    height: 1.33333rem;
    margin-left: 1.28rem;
    text-align: center;
  }

  .hengha {
    font-size: 0.42667rem;
    color: #333333;
    margin-left: 0.24rem;
  }

  .details:last-child {
    display: flex;
    align-items: center;
    position: relative;
  }

  .notice {
    display: block;
    position: absolute;
    font-size: 0.34667rem;
    color: #666666;
    left: 1.28rem;
    top: 1.5rem;
  }

  input,
  textarea {
    font-size: 0.42667rem;
  }

  .videoImg {
    margin-left: 0.4rem;
    width: 6rem;
  }
</style>



